.Search {
    width: 100%;
    height: 100%;
    max-width: 1720px;
    padding: 1rem 2rem;
    // background-color: blue;
    // overflow: hidden;
    .search-option {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 1rem;
        padding-bottom: 1rem;
        
        select {
            padding: 1rem 0.5rem;
            border: 1px solid #ccc;
            border-radius: 8px;
            font-size: 1.2rem;
            color: gray;
        }
        .search-input {
            flex: 1;
            display: flex;
            align-items: center;
            padding: 1rem 1rem;
            border: 1px solid #ccc;
            border-radius: 8px;
            
            input{
                flex: 1;
                border: none;
                outline: none;
                font-size: 1.2rem;
                padding: 0.5rem 0.1rem;
            }
        }
    }

    .search-body {
        position: relative;
        height: calc(100% - 6rem);
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .search-map {
            width: 100%;
            height: 100%;
            border-radius: 10px;
            box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
        }

        
    }
}